/**
 * typesafe definition of a JSON value
 */
export type JsonValue = null | boolean | number | string | JsonArray | JsonObject;

/**
 * typesafe definition of a JSON array
 */
export interface JsonArray extends Array<JsonValue> {
}

/**
 * typesafe definition of a JSON object
 */
export interface JsonObject {
    [key: string]: JsonValue;
}



/**
 * Checks if the given `JsonValue` is a `JsonArray`
 *
 * @returns true if `jsonValue` is a `JsonArray`, false otherwise
 */
export function isJsonArray(jsonValue: JsonValue): jsonValue is JsonArray {
    return Array.isArray(jsonValue);
}

/**
 * Checks if the given `JsonValue` is a `JsonObject`
 *
 * @returns true if `jsonValue` is a `JsonObject`, false otherwise
 */
export function isJsonObject(jsonValue: JsonValue): jsonValue is JsonObject {
    return jsonValue !== null && typeof jsonValue === 'object' && !Array.isArray(jsonValue);
}



/**
 * A subclass of `Error` that indicates an error that occured when mapping a JsonValue.
 * `JsonMappingError` provides the exact path in the object tree where an error occured (`propertyPath`)
 * and details about the error (`propertyError`).
 */
export class JsonMappingError extends Error {

    public readonly propertyError: string;
    public readonly propertyPath: Array<string | number>;

    public constructor(message: string);
    public constructor(chainedError: JsonMappingError, property: number | string);
    public constructor(
        messageOrChainedError: string | JsonMappingError,
        property?: number | string,
    ) {
        let myPropertyError: string;
        let myPropertyPath: Array<string | number>;
        if (messageOrChainedError instanceof JsonMappingError) {
            myPropertyError = messageOrChainedError.propertyError;
            myPropertyPath = messageOrChainedError.propertyPath.slice(0);
            myPropertyPath.unshift(property!);
        } else {
            myPropertyError = messageOrChainedError;
            myPropertyPath = [];
        }
        super(JsonMappingError.createErrorMessage(myPropertyError, myPropertyPath));
        (this as any).__proto__ = JsonMappingError.prototype;
        this.name = 'JsonMappingError';
        this.propertyError = myPropertyError;
        this.propertyPath = myPropertyPath;
    }

    public toString(jsonValueName?: string): string {
        return jsonValueName === undefined ? super.toString() : `${this.name}: ${JsonMappingError.createErrorMessage(this.propertyError, this.propertyPath, jsonValueName)}`;
    }

    private static createErrorMessage(propertyError: string, propertyPath: Array<string | number>, jsonValueName: string = 'jsonValue'): string {
        const pathString = propertyPath.reduce<string>((s, property) => s + propertyAccessor(property), '');
        return `${propertyError} in ${jsonValueName}${pathString}`;
    }

}

/**
 * Throws a `JsonMappingError` with the given `message`.
 */
export function jsonMappingError(message: string): never {
    throw new JsonMappingError(message);
}

/**
 * A `JsonMapper` is a function that takes a `JsonValue` and validates it and/or converts it into something else.
 */
export type JsonMapper<T> = (jsonValue: JsonValue) => T;

/**
 * Returns the given `JsonValue` if it is a boolean, otherwise throws a `JsonMappingError`.
 */
export function expectBoolean(jsonValue: JsonValue): boolean {
    if (typeof jsonValue !== 'boolean') {
        throw new JsonMappingError('boolean expected');
    }
    return jsonValue;
}

/**
 * Returns the given `JsonValue` if it is a number, otherwise throws a `JsonMappingError`.
 */
export function expectNumber(jsonValue: JsonValue): number {
    if (typeof jsonValue !== 'number') {
        throw new JsonMappingError('number expected');
    }
    return jsonValue;
}

/**
 * Returns the given `JsonValue` if it is an integer, otherwise throws a `JsonMappingError`.
 */
export function expectInteger(jsonValue: JsonValue): number {
    if (typeof jsonValue !== 'number' || Math.floor(jsonValue) !== jsonValue) {
        throw new JsonMappingError('integer expected');
    }
    return jsonValue;
}

/**
 * Creates a `JsonMapper` that returns the given `JsonValue` if it is an integer within [min..max] and otherwise throws a `JsonMappingError`.
 */
export function expectIntegerInRange(min: number, max: number): JsonMapper<number> {
    if (min > max) {
        throw new Error('invalid range');
    }
    return jsonValue => {
        const integer = expectInteger(jsonValue);
        if (integer < min || integer > max) {
            throw new JsonMappingError('value out of range');
        }
        return integer;
    };
}

/**
 * Returns the given `JsonValue` if it is a string, otherwise throws a `JsonMappingError`.
 */
export function expectString(jsonValue: JsonValue): string {
    if (typeof jsonValue !== 'string') {
        throw new JsonMappingError('string expected');
    }
    return jsonValue;
}

/**
 * Creates a `JsonMapper` that expects (and returns) the given `value` or throws a `JsonMappingError`.
 */
export function expectValue<T extends boolean | number | string | null>(value: T): JsonMapper<T> {
    return jsonValue => {
        if (jsonValue !== value) {
            throw new JsonMappingError(JSON.stringify(value) + ' expected');
        }
        return value;
    };
}

/**
 * Expects the given `JsonValue` to be a string containing an ISO formatted date and time
 * (e. g. `2017-12-31T23:59:59.999Z`) and converts it to a `Date` object.
 */
export function toDate(jsonValue: JsonValue): Date {
    if (typeof jsonValue !== 'string' || !/^[0-9]{4}-[0-9]{2}-[0-9]{2}T[0-9]{2}:[0-9]{2}:[0-9]{2}\.[0-9]{3}Z$/.test(jsonValue)) {
        throw new JsonMappingError('combined date and time in ISO 8601 format expected');
    }
    return new Date(jsonValue);
}

/**
 * Creates a `JsonMapper` for arrays that maps each element with the given `JsonMapper`.
 */
export function arrayMapper<T>(elementMapper: JsonMapper<T>): JsonMapper<Array<T>> {
    return json => {
        if (!isJsonArray(json)) {
            throw new JsonMappingError('array expected');
        }
        return json.map((value, index) => mapProperty(index, value, elementMapper));
    };
}

/**
 * Maps a `JsonObject` by mapping each property with the given `valueMapper`.
 */
export function mapObjectMapper<T>(valueMapper: JsonMapper<T>): JsonMapper<{ [key: string]: T }> {
    return json => {
        if (!isJsonObject(json)) {
            throw new JsonMappingError('object expected');
        }
        const mapObject: { [key: string]: T } = {};
        for (const key in json) {
            mapObject[key] = mapProperty(key, json[key], valueMapper);
        }
        return mapObject;
    };
}

/**
 * Converts an `ArrayBuffer` to a string in hex format.
 */
export function arrayBufferToHexString(arrayBuffer: ArrayBuffer): string {
    return new Uint8Array(arrayBuffer).reduce((s, b) => s + (b < 16 ? '0' : '') + b.toString(16), '');
}

/**
 * Creates a `JsonMapper` that expects a string in hex format and converts it to an `ArrayBuffer` or throws a `JsonMappingError`.
 *
 * @param checkLength Function that checks `ArrayBuffer`'s length. To indicate an error, `checkLength` returns a falsy value or throws a `JsonMappingError`.
 */
export function arrayBufferMapper(checkLength?: (byteLength: number) => boolean): JsonMapper<ArrayBuffer> {
    return jsonValue => {
        const jsonString = expectString(jsonValue);
        if (jsonString.length % 2 !== 0) {
            throw new JsonMappingError('string length must be even');
        }
        const byteLength = jsonString.length / 2;
        if (checkLength) {
            if (!checkLength(byteLength)) {
                throw new JsonMappingError('invalid length');
            }
        }
        if (!/^([0-9a-fA-F]{2})*$/.test(jsonString)) {
            throw new JsonMappingError('hex string expected');
        }
        const arrayBuffer = new ArrayBuffer(byteLength);
        const view = new Uint8Array(arrayBuffer);
        for (let i = 0; i < byteLength; i++) {
            view[i] = parseInt(jsonString.substr(2 * i, 2), 16);
        }
        return arrayBuffer;
    };
}

/**
 * Used in `objectMapper` and `strictObjectMapper` to provide access to the `JsonObject` that must be mapped.
 */
export interface JsonObjectAccessor {

    /**
     * Maps the property with the given `propertyName` using the given `mapper`.
     * If the property doesn't exist or mapping fails, a `JsonMappingError` will be thrown.
     */
    get<T>(propertyName: string, mapper: JsonMapper<T>): T;

    /**
     * Maps the property with the given `propertyName` using the given `mapper`.
     * If the property doesn't exist, `undefined` will be returned.
     * If mapping fails, a `JsonMappingError` will be thrown.
     */
    getOptional<T>(propertyName: string, mapper: JsonMapper<T>): T | undefined;

    /**
     * Maps the property with the given `propertyName` using the given `mapper`.
     * If the property doesn't exist, the given `defaultValue` will be returned.
     * If mapping fails, a `JsonMappingError` will be thrown.
     */
    getOptional<T>(propertyName: string, mapper: JsonMapper<T>, defaultValue: T): T;

    /**
     * Explicitly ignores the property with the given `propertyName`.
     * This may be helpful when using `strictObjectMapper`.
     */
    ignore(propertyName: string): void;

}

class ObjectMappingHelper implements JsonObjectAccessor {

    protected readonly jsonObject: JsonObject;

    public constructor(jsonValue: JsonValue) {
        if (!isJsonObject(jsonValue)) {
            throw new JsonMappingError('object expected');
        }
        this.jsonObject = jsonValue;
    }

    public get<T>(propertyName: string, mapper: JsonMapper<T>): T {
        const jsonValue = this.jsonObject[propertyName] as JsonValue | undefined;
        if (jsonValue === undefined) {
            throw new JsonMappingError(`missing property ${propertyName}`);
        }
        return mapProperty(propertyName, jsonValue, mapper);
    }

    public getOptional<T>(propertyName: string, mapper: JsonMapper<T>): T | undefined;
    public getOptional<T>(propertyName: string, mapper: JsonMapper<T>, defaultValue: T): T;
    public getOptional<T>(propertyName: string, mapper: JsonMapper<T>, defaultValue?: T): T | undefined {
        const jsonValue = this.jsonObject[propertyName] as JsonValue | undefined;
        return jsonValue === undefined ? defaultValue : mapProperty(propertyName, jsonValue, mapper);
    }

    public ignore(propertyName: string): void {
        // This method doesn't do anything.
        // It only exists because objectMapper and strictObjectMapper should have the same signature.
    }

}

class StrictObjectMappingHelper extends ObjectMappingHelper {

    private readonly expectedProperties: { [kex: string]: boolean } = {};

    public constructor(jsonValue: JsonValue) {
        super(jsonValue);
    }

    public get<T>(propertyName: string, mapper: JsonMapper<T>): T {
        this.expectedProperties['X_' + propertyName] = true;
        return super.get(propertyName, mapper);
    }

    public getOptional<T>(propertyName: string, mapper: JsonMapper<T>): T | undefined;
    public getOptional<T>(propertyName: string, mapper: JsonMapper<T>, defaultValue: T): T;
    public getOptional<T>(propertyName: string, mapper: JsonMapper<T>, defaultValue?: T): T | undefined {
        this.expectedProperties['X_' + propertyName] = true;
        return super.getOptional(propertyName, mapper, defaultValue);
    }

    public ignore(propertyName: string): void {
        this.expectedProperties['X_' + propertyName] = true;
    }

    public failOnUnexpectedProperties(): void {
        for (const propertyName in this.jsonObject) {
            if (!this.expectedProperties['X_' + propertyName]) {
                throw new JsonMappingError(`unexpected property ${JSON.stringify(propertyName)}`);
            }
        }
    }

}

/**
 * Creates a `JsonMapper` that uses the given `factory` to map a `JsonObject` to some value.
 * If the `JsonValue` to be mapped is not a `JsonObject`, a `JsonMappingError` will be thrown.
 * The factory doesn't access the `JsonObject` directly.
 * Instead an `accessor` gets passed that must be used to access and map the `JsonObject`'s properties.
 */
export function objectMapper<T>(factory: (accessor: JsonObjectAccessor) => T): JsonMapper<T> {
    return (jsonValue: JsonValue) => factory(new ObjectMappingHelper(jsonValue));
}

/**
 * Creates a `JsonMapper` that uses the given `factory` to map a `JsonObject` to some value.
 * If the `JsonValue` to be mapped is not a `JsonObject`, a `JsonMappingError` will be thrown.
 * The factory doesn't access the `JsonObject` directly.
 * Instead an `accessor` gets passed that must be used to access and map the `JsonObject`'s properties.
 * There is only a singe difference between `objectMapper` and `strictObjectMapper`:
 * `strictObjectMapper` throws a `JsonMappingError` if the `JsonObject` to be mapped has a property
 * that is not accessed by the given `factory`.
 */
export function strictObjectMapper<T>(factory: (accessor: JsonObjectAccessor) => T): JsonMapper<T> {
    return (jsonValue: JsonValue) => {
        const accessor = new StrictObjectMappingHelper(jsonValue);
        const result = factory(accessor);
        accessor.failOnUnexpectedProperties();
        return result;
    };
}

function enumNumberValueSet(theEnum: { [prop: string]: any }): { [value: string]: true } {
    const set: { [value: string]: true } = {};
    for (const prop in theEnum) {
        if (!theEnum.hasOwnProperty(prop)) {
            throw new Error('argument is not a typescript enum because it has a prototype with enumerable properties');
        }
        const value = theEnum[prop];
        if (typeof value === 'number') {
            set[value] = true;
        } else if (typeof value !== 'string') {
            throw new Error('argument is not a typescript enum because it has a property which is not of type number and not of type string');
        }
    }
    return set;
}

/**
 * Creates a `JsonMapper` for the given enum.
 */
export function enumMapperByValue<E>(theEnum: { [prop: string]: any }): JsonMapper<E> {
    const numberValueSet = enumNumberValueSet(theEnum);

    const stringValueSet: { [value: string]: true } = {};
    for (const prop in theEnum) {
        const value = theEnum[prop];
        if (typeof value === 'string' && !numberValueSet[prop]) {
            stringValueSet[value] = true;
        }
    }

    return (json: JsonValue) => {
        if (typeof json === 'number' && numberValueSet[json] || typeof json === 'string' && stringValueSet[json]) {
            return json as any as E;
        }
        throw new JsonMappingError('enum value expected');
    };
}

/**
 * Creates a `JsonMapper` for the given enum to be used if the enum names instead of values are stored.
 */
export function enumMapperByIdentifier<E>(theEnum: { [prop: string]: any }): JsonMapper<E> {
    const numberValueSet = enumNumberValueSet(theEnum);

    const map: { [identifier: string]: E } = {};
    for (const prop in theEnum) {
        if (!numberValueSet[prop]) {
            map[prop] = theEnum[prop] as E;
        }
    }

    return (json: JsonValue) => {
        const value = typeof json === 'string' ? map[json] : undefined;
        if (value === undefined) {
            throw new JsonMappingError('enum identifier expected');
        }
        return value;
    };
}

/**
 * Creates a `JsonMapper` that
 * * expects its given `JsonValue` to be an object,
 * * expects this object to have a property which name equals the `typeProp` argument,
 * * expects this property to have a string value,
 * * uses this string as key to select a mapper in the given `mappers` argument,
 * * maps the `JsonValue` using the selected mapper.
 */
export function delegatingMapper<T>(typeProp: string, mappers: { [type: string]: JsonMapper<T> }): JsonMapper<T> {
    const mapperSelector = objectMapper(accessor => accessor.get(typeProp, jsonValue => mappers[expectString(jsonValue)] || jsonMappingError('unexpected value')));
    return jsonValue => mapperSelector(jsonValue)(jsonValue);
}

function mapProperty<T>(propertyName: number | string, propertyValue: JsonValue, mapper: JsonMapper<T>): T {
    try {
        return mapper(propertyValue);
    } catch (e) {
        if (e instanceof JsonMappingError) {
            throw new JsonMappingError(e, propertyName);
        } else {
            throw e;
        }
    }
}

function propertyAccessor(property: number | string): string {
    if (typeof property === 'number') {
        return '[' + property.toString() + ']';
    }
    if (/^[a-zA-Z_][a-zA-Z0-9_]*$/.test(property)) {
        return '.' + property;
    }
    return '[' + JSON.stringify(property) + ']';
}
